import styled from "@emotion/styled";

export const InputWrapper = styled.div`
display: flex;
flex-direction: column;
font-weight:700;
font-size:18px;
`
export const Input = styled.input`
width:200px;
margin-top:4px;
margin-bottom:4px;
padding:5px;
font: inherit;
font-size:18px;
border-radius: 4px;
border:${(props) => `3px solid ${props.theme.colors.BorderColor}`};
background-color:${(props) => props.theme.colors.InputColor};
transition: ${(props) =>
      `transform${props.theme.colors.animation} ${props.theme.colors.time}`},
    ${(props) =>
      `box-shadow${props.theme.colors.animation} ${props.theme.colors.time}`};
  &:hover,
  &:focus {
    box-shadow: ${(props) => `0 0 10px${props.theme.colors.white}`};
    transform: scale(1);
  }

`
export const  FormButton = styled.button`
margin: 0;
margin-top:5px;
padding: 5px 10px;
font: inherit;
border-radius: 4px;
font-weight:700;
font-size:18px;
border:${(props) => `3px solid ${props.theme.colors.BorderColor}`};
background-color:${(props) =>props.theme.colors.ButtonColor};
cursor:pointer;
  transition: ${(props) =>
      `transform${props.theme.animation} ${props.theme.time}`},
    ${(props) =>
      `box-shadow${props.theme.animation} ${props.theme.time}`};
  &:hover,
  &:focus {
    box-shadow: ${(props) => `0 0 10px${props.theme.colors.white}`};
    transform: scale(1.1);
  }
`